html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background: linear-gradient(to right, #DDE2E4 0%, #FFFFFF 30%, #FFFFFF 70%, #DDE2E4 100%);
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

.waby {
  display: flex;
  align-items: center;
  justify-content: center;
}
.waby h2 {
  letter-spacing: 2vmin;
  font-family: "Work Sans", sans-serif;
  font-size: 15vmin;
  position: absolute;
  top: 3rem;
}
.waby .border {
  color: white;
  text-shadow: -1px -1px 0 black, 1px -1px 0 black, -1px 1px 0 black, 1px 1px 0 black;
}
.waby .wave {
  color: black;
  animation: wave 3s ease-in-out infinite;
}
@keyframes wave {
  0%, 100% {
    clip-path: polygon(0% 47%, 10% 48%, 33% 54%, 54% 60%, 70% 61%, 84% 59%, 100% 52%, 100% 100%, 0% 100%);
  }
  50% {
    clip-path: polygon(0% 60%, 15% 65%, 34% 66%, 51% 62%, 67% 50%, 84% 45%, 100% 46%, 100% 100%, 0% 100%);
  }
}

/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background: linear-gradient(to right, #FFFFFF 0%, #f4f5f6 50%, #FFFFFF 100%);
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.4rem;
  max-width: 80rem;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid #a90000;
  color: #a90000;
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid #007800;
  color: #007800;
}

.login {
  margin-top: 12rem;
}
.login .wave {
  color: #7da7d9;
  animation: wave 3s ease-in-out infinite;
}
.login input[type=submit] {
  background-color: #7da7d9;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #568ccd;
  cursor: pointer;
}
.login .acciones a {
  color: #568ccd;
}

.crear {
  margin-top: 12rem;
}
.crear .wave {
  color: #a286c0;
  animation: wave 3s ease-in-out infinite;
}
.crear input[type=submit] {
  background-color: #a286c0;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .crear input[type=submit] {
    width: auto;
  }
}
.crear input[type=submit]:hover {
  background-color: #8864af;
  cursor: pointer;
}
.crear .acciones a {
  color: #8864af;
}

.olvide {
  margin-top: 12rem;
}
.olvide .wave {
  color: #f4989d;
  animation: wave 3s ease-in-out infinite;
}
.olvide input[type=submit] {
  background-color: #f4989d;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .olvide input[type=submit] {
    width: auto;
  }
}
.olvide input[type=submit]:hover {
  background-color: #ef6a71;
  cursor: pointer;
}
.olvide .acciones a {
  color: #ef6a71;
}

.reestablecer {
  margin-top: 12rem;
}
.reestablecer .wave {
  color: #8493ca;
  animation: wave 3s ease-in-out infinite;
}
.reestablecer input[type=submit] {
  background-color: #8493ca;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .reestablecer input[type=submit] {
    width: auto;
  }
}
.reestablecer input[type=submit]:hover {
  background-color: #6074bb;
  cursor: pointer;
}
.reestablecer .acciones a {
  color: #6074bb;
}

.mensaje {
  margin-top: 12rem;
}
.mensaje .wave {
  color: #bd8dbf;
  animation: wave 3s ease-in-out infinite;
}

.confirmar {
  margin-top: 12rem;
}
.confirmar .wave {
  color: #8077d1;
  animation: wave 3s ease-in-out infinite;
}
.confirmar .acciones {
  justify-content: center;
}
.confirmar .acciones a {
  color: #5c51c4;
  font-size: 2rem;
}

.dashboard {
  background: #f4f5f6;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
  overflow-x: hidden;
}
.dashboard .contenido {
  padding: 3rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}
.dashboard .formulario input:not([type=submit]) {
  background: #DDE2E4;
}
.dashboard .formulario input[type=submit] {
  background-color: #8077d1;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.4rem;
  border-radius: 0.4rem;
}
@media (min-width: 768px) {
  .dashboard .formulario input[type=submit] {
    width: auto;
  }
}
.dashboard .formulario input[type=submit]:hover {
  background-color: #5c51c4;
  cursor: pointer;
}
.dashboard .enlace {
  color: #5c51c4;
}
.dashboard .enlace:hover {
  color: #352c83;
}

.sidebar {
  opacity: 0;
  background-color: #8077d1;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  padding: 2rem;
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.93);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: #8077d1;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 3.5rem;
}
.sidebar .contenedor-sidebar .cerrar-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  padding: 1rem;
  transition-property: background-color;
  transition-duration: 300ms;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.4rem;
  margin-bottom: 2rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #4439a9;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.7rem;
    margin-bottom: 1.5rem;
    text-align: left;
  }
}
.sidebar .cerrar-sesion-mobile {
  margin-top: 10rem;
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion {
  background-color: #251f5d;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile .cerrar-sesion {
    width: auto;
  }
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion:hover {
  background-color: #161237;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile {
    display: none;
  }
}

.barra-mobile {
  background-color: #8077d1;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  color: #FFFFFF;
  margin: 0;
}
.barra-mobile .menu img {
  width: 5rem;
}
.barra-mobile .menu img:hover {
  cursor: pointer;
}

.barra {
