    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #352c83;
  cursor: pointer;
}

.no-proyectos a {
  color: #8077d1;
}

.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  display: flex;
}
.listado-proyectos .proyecto a {
  color: #FFFFFF;
