  min-height: 12rem;
  width: 100%;
  border-radius: 0.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.7rem;
}
.listado-proyectos .proyecto a:hover {
  background-color: #699ad3;
}

.contenedor-nueva-tarea {
  display: flex;
