  }
}
.contenedor-nueva-tarea .agregar-tarea:hover {
  background-color: #568ccd;
  cursor: pointer;
}

.listado-tareas {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
}
.listado-tareas .no-tareas {
  text-align: center;
}

.tarea {
  padding: 0.5rem 2rem;
  border-bottom: 2px solid #cfd6d9;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tarea:last-of-type {
  border: none;
}
.tarea p {
  font-size: 1.5rem;
}
.tarea .opciones {
  display: flex;
  gap: 1rem;
}
.tarea .opciones button {
  border: none;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 1rem;
}
.tarea .opciones button:hover {
  cursor: pointer;
}
.tarea .opciones .completada {
  background-color: #7da7d9;
  color: #FFFFFF;
}
.tarea .opciones .pendiente {
  background-color: #f4989d;
  color: #FFFFFF;
}
.tarea .opciones .en-proceso {
  background-color: #a5aebb;
  color: #FFFFFF;
}
.tarea .opciones .eliminar-tarea {
  background-color: #e2404c;
  color: #FFFFFF;
  padding: 0.2rem 0.8rem 0.3rem 0.8rem;
  font-size: 1rem;
  border-radius: 50px;
  text-align: center;
  font-weight: 900;
  height: 23px;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 500ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
  border-radius: 1rem;
}
.modal .formulario legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .formulario input[type=text] {
  border: 1px solid #6b7280;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
}
.modal .formulario .opciones {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}
.modal .formulario .submit-nueva-tarea {
  background-color: #bd8dbf;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nueva-tarea {
    width: auto;
  }
}
.modal .formulario .submit-nueva-tarea:hover {
  background-color: #aa6cad;
  cursor: pointer;
}
.modal .formulario .cerrar-modal {
  background-color: #f4989d;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.4rem;
  border-radius: 0.4rem;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: #ef6a71;
  cursor: pointer;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}

.filtros {
  background-color: #FFFFFF;
  margin-top: 2rem;
  border-radius: 1rem;
  padding: 2rem;
}
@media (min-width: 1024px) {
  .filtros .filtros-imputs {
    display: flex;
    align-items: center;
    gap: 3rem;
  }
}
.filtros h2 {
  color: #8077d1;
  font-size: 2.6rem;
}
@media (min-width: 768px) {
  .filtros h2 {
    margin: 0;
  }
}
.filtros .campo {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .filtros .campo {
    margin-bottom: 0;
  }
}
.filtros input[type=radio] {
  accent-color: #5c51c4;
  width: 1.6rem;
  height: 1.6rem;
}

@media (prefers-color-scheme: dark) {
  body {
    background: #2c2c3b;
  }

  .tagline,
.formulario label,
.login .acciones a,
.crear .acciones a,
.olvide .acciones a,
.listado-tareas p {
    color: #FFFFFF;
  }

  .descripcion-pagina,
.nombre-pagina,
.filtros-imputs label {
    color: #DDE2E4;
  }

  .formulario .campo input {
    background: #4d4d67;
  }
  .formulario .campo input::placeholder {
    color: #8899a0;
  }

  .alerta.error {
    background: #FFFFFF;
  }
}
@media (prefers-color-scheme: dark) and (min-width: 768px) {
  .sidebar {
    background: #16161e;
  }
}
@media (prefers-color-scheme: dark) {
  .sidebar .sidebar-nav a.activo, .sidebar .sidebar-nav a:hover {
    background: #6074bb;
  }
}
@media (prefers-color-scheme: dark) {
  .barra-mobile,
.barra {
    background: #1d1d27;
  }
}
@media (prefers-color-scheme: dark) {
  .barra p {
    color: #DDE2E4;
  }
  .barra p span {
    color: #8493ca;
  }
}
@media (prefers-color-scheme: dark) {
  .filtros {
    background: #4d4d67;
  }
  .filtros h2 {
    color: #FFFFFF;
  }
}
@media (prefers-color-scheme: dark) {
  .dashboard {
    background: #2c2c3b;
  }
  .dashboard .enlace {
    color: #DDE2E4;
  }
  .dashboard .enlace:hover {
    color: #a4b2b7;
  }
  .dashboard .formulario input:not([type=submit]) {
    background: #424258;
    color: #FFFFFF;
  }
  .dashboard .formulario input:not([type=submit])::placeholder {
    color: #8899a0;
  }
}
@media (prefers-color-scheme: dark) {
  .listado-proyectos .proyecto a {
    background: #585875;
  }
  .listado-proyectos .proyecto a:hover {
    background: #424258;
  }
}
@media (prefers-color-scheme: dark) {
  .listado-tareas .tarea {
    background: #333344;
    border: none;
    color: #DDE2E4;
  }
}
@media (prefers-color-scheme: dark) {
  .perfil .formulario .campo input {
    color: #6b7280;
  }
}
@media (prefers-color-scheme: dark) {
  .modal .formulario {
    background: #2c2c3b;
  }
  .modal .formulario input[type=text] {
    background: #424258;
    border: none;
    color: #FFFFFF;
  }
  .modal .formulario input[type=text]::placeholder {
    color: #a4b2b7;
  }
  .modal .formulario legend {
    color: #DDE2E4;
  }
}